<template>
  <div id="app">
    <img class='cover' :src="`${publicPath}/imgs/${data.cover}`" alt="" @click='makeMagic'>
    <div class='con'>
      <h2>{{data.title}}</h2>
      <h4 v-for="(item, index) in data.desps" :key="item" :class="{'last-desp': index === data.desps.length - 1}">{{item}}</h4>
      <Card v-for="card in data.list" :key="card.date" :card="card"></Card>
      <Cover v-if="showMagic"></Cover>
    </div>
  </div>
</template>

<script>
import Card from './components/Card.vue'
import Cover from './components/Cover.vue'
import data from './config/data.js'

export default {
  name: 'App',
  components: {
    Card,
    Cover
  },
  data () {
    window.vm = this
    return {
      data,
      showMagic: false
    }
  },
  computed: {
    publicPath () {
      return process.env.NODE_ENV === 'production' ? '/timeline/assets' : '/assets'
    }
  },
  methods: {
    makeMagic () {
      this.showMagic = true
      setTimeout(() => {
        this.reverse()
        setTimeout(() => {
          this.showMagic = false
        }, 2000)
      }, 5000)
    },
    reverse () {
      this.data.list = this.data.list.reverse()
    }
  }
}
</script>

