<template>
  <div class='card'>
    <div class='card-tail'></div>
    <div class="card-head"></div>
    <div class='card-content'>
      <h3 class='date'>{{card.date}}</h3>
      <p v-for="(item, index) in card.messages" :key="index" :class="{'has-avatar': isLuomie(item) || isXiaoqi(item)}">
        <img :src="`${publicPath}/luomie.jpeg`" alt="" class='avatar' v-if="isLuomie(item)">
        <img :src="`${publicPath}/xiaoqi.jpeg`" alt="" class='avatar' v-if="isXiaoqi(item)">
        <span>{{item}}</span>
      </p>
      <h3 v-if='card.imgAlt'>{{card.imgAlt}}</h3>
      <img v-for="item in card.imgs" :key="item" :src="`${publicPath}/imgs/${item}`" :alt="card.imgAlt || ''">
    </div>
  </div>
</template>

<script>
export default {
  name: 'Card',
  props: {
    card: Object
  },
  computed: {
    publicPath () {
      return process.env.NODE_ENV === 'production' ? '/timeline/assets' : '/assets'
    }
  },
  methods: {
    isLuomie (message) {
      const r = /^洛咩：/
      return r.test(message)
    },
    isXiaoqi (message) {
      const r = /^小七：/
      return r.test(message)
    }
  },
  data () {
    return {}
  }
}
</script>

